import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import SpeciesItem from './SpeciesItem';
import './SpeciesCont.scss'
import RouteContext from '../context/RouteProvider';
import LocContext from '../context/LocProvider';


function SpeciesCont(props) {
  const [speciesData, speciesDataSet] = useState(null)

  var query = "https://us-central1-species-eebec.cloudfunctions.net/app"
  const loc = useContext(RouteContext).match.params.lugar
  const onLocChange = useContext(LocContext).locInfo.onLocChange
  if(loc){
    onLocChange(loc)
    query += `?lugar=${loc}`
  } else {
    query += `?lugar=Chajarí`
    console.log("ERROR - SpeciesCont: No loc name")
  }

  const getSpecies = () => {
    axios
      .get("")
      .then((response) => {
        const dataResp = response.data
        speciesDataSet(JSON.parse(dataResp))
      })
      .catch((error) => {
        console.log("ERROR!!! : ", error)
        //speciesDataSet(JSON.parse(testData))
      })
  }
  
  useEffect(() => {
    getSpecies()
  }, [])

  const speciesItemList = []
  if(speciesData){
    for(const species of speciesData){
      speciesItemList.push(
        <SpeciesItem species={species}/>
      )
    }
  }
  
  return (
    <div className="speciesCont">
      {speciesItemList}
    </div>
  )
}

export default SpeciesCont;